:root {
  --font-family: "Pretendard Variable", Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
  --font-size-xs: .75rem;
  --font-size-sm: .875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --color-background: #0f172a;
  --color-surface: #1e293bcc;
  --color-surface-solid: #1e293b;
  --color-surface-hover: #334155e6;
  --color-surface-active: #0f172af2;
  --color-input: #33415599;
  --color-input-solid: #334155;
  --color-primary: #0ea5e9;
  --color-primary-hover: #38bdf8;
  --color-primary-active: #0284c7;
  --color-secondary: #f59e0b;
  --color-secondary-hover: #fbbf24;
  --color-accent: #f97316;
  --color-text: #f1f5f9;
  --color-text-muted: #94a3b8;
  --color-text-dark: #1e293b;
  --color-border: #94a3b833;
  --color-toggle-off: #64748b;
  --color-toggle-on: #f1f5f9;
  --color-notice-bg: #fffffff2;
  --color-notice-header: #fbbf24;
  --color-toast-bg: #e2e8f0;
  --color-copyright: #ffffffb3;
  --color-focus: #0ea5e9;
  --focus-ring: 0 0 0 2px #0ea5e980;
  --space-1: .25rem;
  --space-2: .5rem;
  --space-3: .75rem;
  --space-4: 1rem;
  --space-5: 1.25rem;
  --space-6: 1.5rem;
  --space-8: 2rem;
  --space-xs: var(--space-1);
  --space-sm: var(--space-2);
  --space-md: var(--space-4);
  --space-lg: var(--space-6);
  --space-xl: var(--space-8);
  --radius-sm: .25rem;
  --radius-md: .375rem;
  --radius-lg: .5rem;
  --radius-xl: .75rem;
  --radius-2xl: 1rem;
  --radius-full: 9999px;
  --transition-fast: .15s cubic-bezier(.4, 0, .2, 1);
  --transition-base: .2s cubic-bezier(.4, 0, .2, 1);
  --transition-slow: .3s cubic-bezier(.4, 0, .2, 1);
  --shadow-sm: 0 1px 2px 0 #0000000d;
  --shadow-md: 0 4px 6px -1px #0000001a, 0 2px 4px -2px #0000001a;
  --shadow-lg: 0 10px 15px -3px #0000001a, 0 4px 6px -4px #0000001a;
  --shadow-xl: 0 20px 25px -5px #0000001a, 0 8px 10px -6px #0000001a;
  --glass-blur: 12px;
  --glass-bg: #0f172abf;
  --glass-border: #94a3b81a;
}

:root.dark {
  --color-background: #0f172a;
  --color-surface: #1e293bcc;
  --color-surface-solid: #1e293b;
  --color-surface-hover: #334155e6;
  --color-surface-active: #0f172af2;
  --color-input: #33415599;
  --color-text: #f1f5f9;
  --color-text-muted: #94a3b8;
  --color-border: #94a3b833;
  --color-copyright: #ffffffb3;
  --color-focus: #0ea5e9;
  --glass-bg: #0f172abf;
}

:root.light {
  --color-background: #f8fafc;
  --color-surface: #fffc;
  --color-surface-solid: #fff;
  --color-surface-hover: #f1f5f9e6;
  --color-surface-active: #e2e8f0f2;
  --color-input: #f1f5f9cc;
  --color-input-solid: #f1f5f9;
  --color-text: #1e293b;
  --color-text-muted: #64748b;
  --color-text-dark: #1e293b;
  --color-border: #94a3b84d;
  --color-toggle-off: #cbd5e1;
  --color-copyright: #0009;
  --color-focus: #0284c7;
  --glass-bg: #ffffffbf;
  --glass-border: #94a3b833;
}

@media (prefers-color-scheme: light) {
  :root:not(.light):not(.dark) {
    --color-background: #f8fafc;
    --color-surface: #fffc;
    --color-surface-solid: #fff;
    --color-surface-hover: #f1f5f9e6;
    --color-surface-active: #e2e8f0f2;
    --color-input: #f1f5f9cc;
    --color-input-solid: #f1f5f9;
    --color-text: #1e293b;
    --color-text-muted: #64748b;
    --color-text-dark: #1e293b;
    --color-border: #94a3b84d;
    --color-toggle-off: #cbd5e1;
    --color-copyright: #0009;
    --color-focus: #0284c7;
    --glass-bg: #ffffffbf;
    --glass-border: #94a3b833;
  }
}

* {
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background-color var(--transition-base), color var(--transition-base);
  line-height: 1.5;
}

:focus {
  outline: none;
}

:focus-visible {
  outline: 2px solid var(--color-focus);
  outline-offset: 2px;
}

button:focus-visible, select:focus-visible, input:focus-visible, textarea:focus-visible {
  outline: 2px solid var(--color-focus);
  outline-offset: 2px;
  box-shadow: var(--focus-ring);
}

input[type="checkbox"]:focus-visible {
  outline: none;
}

input[type="checkbox"]:focus-visible:before {
  box-shadow: var(--focus-ring);
}

canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
}

div.copyright {
  color: var(--color-copyright);
  font-size: var(--font-size-xs);
  z-index: 999;
  text-align: center;
  width: 90%;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

div.copyright a {
  color: var(--color-copyright);
}

.icon {
  vertical-align: middle;
  background: currentColor;
  width: 25px;
  height: 25px;
  display: inline-block;
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
}

.icon.play {
  mask-image: url("play.d2b98249.svg");
}

.icon.shuffle {
  mask-image: url("shuffle.e9e498cf.svg");
}

.icon.megaphone {
  mask-image: url("megaphone.da49dfc8.svg");
}

.icon.record {
  mask-image: url("record.c71c4728.svg");
}

.icon.map {
  mask-image: url("map.eb357c03.svg");
}

.icon.trophy {
  mask-image: url("trophy.50d1ba0a.svg");
}

.icon.bomb {
  mask-image: url("bomb.455f9b17.svg");
}

.icon.sun {
  mask-image: url("sun.ab6ec46d.svg");
}

.icon.moon {
  mask-image: url("moon.3f475bd2.svg");
}

#settings {
  top: var(--space-4);
  left: var(--space-4);
  z-index: 999;
  background: var(--glass-bg);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-xl);
  gap: var(--space-4);
  padding: var(--space-5);
  visibility: visible;
  opacity: 1;
  min-width: 320px;
  max-width: 640px;
  transition: visibility 0s, opacity var(--transition-slow), transform var(--transition-slow);
  flex-direction: column;
  display: flex;
  position: fixed;
  transform: translateY(0);
}

#settings.hide {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
}

#settings .settings-header {
  padding-bottom: var(--space-3);
  border-bottom: 1px solid var(--color-border);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#settings .settings-title {
  align-items: center;
  gap: var(--space-2);
  display: flex;
}

#settings .settings-title .title-accent {
  background: var(--color-primary);
  border-radius: var(--radius-full);
  width: 4px;
  height: 24px;
}

#settings .settings-title h1 {
  font-size: var(--font-size-xl);
  color: var(--color-text);
  letter-spacing: -.02em;
  margin: 0;
  font-weight: 700;
}

#settings .header-controls {
  align-items: center;
  gap: var(--space-3);
  display: flex;
}

#settings .theme-toggle {
  align-items: center;
  gap: var(--space-2);
  display: flex;
}

#settings .theme-toggle .icon {
  width: 16px;
  height: 16px;
  color: var(--color-text-muted);
}

#settings .settings-grid {
  gap: var(--space-5);
  grid-template-columns: 1fr 1fr;
  display: grid;
}

#settings .settings-left {
  gap: var(--space-3);
  flex-direction: column;
  display: flex;
}

#settings .input-header {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

#settings .input-header label {
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
  font-weight: 600;
}

#settings .input-header .count-badge {
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
  background: var(--color-surface-hover);
  border-radius: var(--radius-md);
  padding: 2px 8px;
}

#settings textarea {
  border: 1px solid var(--color-border);
  background: var(--color-surface-active);
  width: 100%;
  min-height: 120px;
  max-height: 200px;
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  border-radius: var(--radius-xl);
  padding: var(--space-3);
  color: var(--color-text);
  resize: none;
  transition: border-color var(--transition-fast), box-shadow var(--transition-fast);
}

#settings textarea::placeholder {
  color: var(--color-text-muted);
}

#settings textarea:hover {
  border-color: var(--color-text-muted);
}

#settings textarea:focus {
  border-color: var(--color-primary);
  box-shadow: 0 0 0 1px var(--color-primary);
  outline: none;
}

#settings textarea::-webkit-scrollbar {
  width: 8px;
}

#settings textarea::-webkit-scrollbar-track {
  background: var(--color-surface-active);
  border-radius: 4px;
}

#settings textarea::-webkit-scrollbar-thumb {
  background: var(--color-text-muted);
  border-radius: 4px;
}

#settings .actions-grid {
  gap: var(--space-2);
  grid-template-columns: auto 1fr;
  display: grid;
}

#settings .settings-right {
  gap: var(--space-3);
  padding-left: var(--space-5);
  border-left: 1px solid var(--color-border);
  flex-direction: column;
  display: flex;
}

#settings .option-item {
  gap: var(--space-2);
  flex-direction: column;
  display: flex;
}

#settings .option-item.option-toggle {
  padding: var(--space-2);
  border-radius: var(--radius-lg);
  transition: background var(--transition-fast);
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#settings .option-item.option-toggle:hover {
  background: var(--color-surface-hover);
}

#settings .option-label {
  align-items: center;
  gap: var(--space-2);
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
  font-weight: 500;
  display: flex;
}

#settings .option-label .icon {
  width: 16px;
  height: 16px;
}

#settings .winner-controls {
  align-items: center;
  gap: var(--space-2);
  display: flex;
}

#settings .winner-controls input[type="number"] {
  border: 1px solid var(--color-border);
  background: var(--color-surface-hover);
  border-radius: var(--radius-lg);
  width: 60px;
  height: 32px;
  padding: 0 var(--space-2);
  text-align: center;
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  color: var(--color-text);
  appearance: textfield;
}

#settings .winner-controls input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#settings .winner-controls input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#settings .winner-controls input[type="number"]:focus {
  border-color: var(--color-primary);
  outline: none;
}

#settings button {
  color: var(--color-text-muted);
  background: var(--color-surface-hover);
  border-radius: var(--radius-lg);
  padding: var(--space-2) var(--space-3);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  cursor: pointer;
  transition: all var(--transition-fast);
  justify-content: center;
  align-items: center;
  gap: var(--space-1);
  border: none;
  font-weight: 500;
  display: inline-flex;
}

#settings button:hover {
  background: var(--color-surface-active);
  color: var(--color-text);
}

#settings button:active {
  transform: scale(.98);
}

#settings .btn-primary {
  background: var(--color-primary);
  color: #fff;
  padding: var(--space-3) var(--space-4);
  font-weight: 700;
  box-shadow: 0 4px 14px #0ea5e94d;
}

#settings .btn-primary:hover {
  background: var(--color-primary-hover);
  transform: translateY(-1px);
}

#settings .btn-primary:active {
  transform: translateY(0)scale(.98);
}

#settings .btn-icon {
  padding: var(--space-2);
  background: var(--color-surface-hover);
  border-radius: var(--radius-lg);
}

#settings .btn-icon:hover {
  background: var(--color-surface-active);
}

#settings .btn-icon-text {
  padding: var(--space-3);
  background: var(--color-surface-hover);
}

#settings .btn-icon-text:hover {
  background: var(--color-surface-active);
}

#settings select {
  border-radius: var(--radius-lg);
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  width: 100%;
  height: 36px;
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  color: var(--color-text);
  cursor: pointer;
  padding: 0 var(--space-3);
  transition: all var(--transition-fast);
}

#settings select:hover {
  background: var(--color-surface-active);
}

#settings select:focus {
  border-color: var(--color-primary);
  outline: none;
}

#settings .toggle-switch {
  width: 40px;
  height: 20px;
  position: relative;
}

#settings .toggle-switch input[type="checkbox"] {
  opacity: 0;
  cursor: pointer;
  z-index: 1;
  width: 40px;
  height: 20px;
  position: absolute;
}

#settings .toggle-switch input[type="checkbox"]:checked + label {
  background: var(--color-primary);
}

#settings .toggle-switch input[type="checkbox"]:checked + label:after {
  border-color: var(--color-primary);
  transform: translateX(20px);
}

#settings .toggle-switch label {
  background: var(--color-surface-active);
  border-radius: var(--radius-full);
  cursor: pointer;
  width: 40px;
  height: 20px;
  transition: background var(--transition-fast);
  position: absolute;
  top: 0;
  left: 0;
}

#settings .toggle-switch label:after {
  content: "";
  border: 2px solid var(--color-border);
  border-radius: var(--radius-full);
  width: 20px;
  height: 20px;
  transition: all var(--transition-fast);
  box-sizing: border-box;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

#settings .theme-toggle input[type="checkbox"] {
  appearance: none;
  cursor: pointer;
  background: var(--color-surface-active);
  border-radius: var(--radius-full);
  width: 40px;
  height: 20px;
  transition: background var(--transition-fast);
  position: relative;
}

#settings .theme-toggle input[type="checkbox"]:before {
  display: none;
}

#settings .theme-toggle input[type="checkbox"]:after {
  content: "";
  border: 2px solid var(--color-border);
  border-radius: var(--radius-full);
  width: 20px;
  height: 20px;
  transition: all var(--transition-fast);
  box-sizing: border-box;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
}

#settings .theme-toggle input[type="checkbox"]:checked {
  background: var(--color-primary);
}

#settings .theme-toggle input[type="checkbox"]:checked:after {
  border-color: var(--color-primary);
  transform: translateX(20px);
}

#settings .btn-group {
  background: var(--color-surface-hover);
  border-radius: var(--radius-lg);
  padding: 4px;
  display: flex;
}

#settings .btn-group button {
  padding: var(--space-2) var(--space-3);
  border-radius: var(--radius-md);
  font-size: var(--font-size-xs);
  color: var(--color-text-muted);
  background: none;
  flex: 1;
  font-weight: 500;
}

#settings .btn-group button:hover {
  color: var(--color-text);
  background: none;
}

#settings .btn-group button.active {
  background: var(--color-primary);
  color: #fff;
  box-shadow: var(--shadow-sm);
  font-weight: 700;
}

#settings .btn-group button.active:before {
  display: none;
}

#settings .settings-footer {
  padding-top: var(--space-4);
  border-top: 1px solid var(--color-border);
  justify-content: center;
  display: flex;
}

#settings .recap-link {
  align-items: center;
  gap: var(--space-2);
  color: var(--color-text-muted);
  font-size: var(--font-size-sm);
  transition: color var(--transition-fast);
  font-weight: 500;
  text-decoration: none;
  display: inline-flex;
}

#settings .recap-link:hover {
  color: var(--color-secondary);
}

#settings .recap-link:hover .icon-history {
  transform: rotate(-45deg);
}

#settings .recap-link .icon-history {
  width: 16px;
  height: 16px;
  transition: transform var(--transition-base);
  background: currentColor;
  mask-image: url("record.c71c4728.svg");
  mask-size: contain;
  mask-repeat: no-repeat;
}

#notice {
  background: var(--color-notice-bg);
  width: 480px;
  max-width: 90%;
  -webkit-backdrop-filter: blur(var(--glass-blur));
  border-radius: var(--radius-2xl);
  z-index: 1001;
  color: var(--color-text-dark);
  box-shadow: var(--shadow-xl);
  flex-direction: column;
  padding: 0;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

#notice h1 {
  background: linear-gradient(135deg, var(--color-secondary), var(--color-accent));
  padding: var(--space-4);
  font-size: var(--font-size-lg);
  color: #fff;
  align-items: center;
  margin: 0;
  font-weight: 700;
  display: flex;
}

#notice h1:before {
  content: "";
  width: 1.25em;
  height: 1.25em;
  margin-right: var(--space-2);
  filter: brightness(0) invert();
  background-image: url("megaphone.da49dfc8.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
}

#notice div.notice-body {
  padding: var(--space-4);
  font-size: var(--font-size-sm);
  line-height: 1.6;
}

#notice div.notice-body h2 {
  margin: 0 0 var(--space-3) 0;
  font-size: var(--font-size-base);
  font-weight: 600;
}

#notice div.notice-body p {
  margin: var(--space-2) 0;
}

#notice div.notice-body .recap-button {
  margin: var(--space-4) auto 0;
  max-width: 300px;
  display: block;
}

#notice div.notice-body .recap-button img {
  border-radius: var(--radius-lg);
  width: 100%;
  height: auto;
  box-shadow: var(--shadow-md);
  transition: transform var(--transition-fast);
}

#notice div.notice-body .recap-button img:hover {
  transform: scale(1.02);
}

#notice div.notice-action {
  padding: var(--space-4);
  border-top: 1px solid #0000001a;
  display: flex;
}

#notice div.notice-action button {
  color: #fff;
  background: var(--color-primary);
  border-radius: var(--radius-lg);
  padding: var(--space-3) var(--space-4);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  cursor: pointer;
  transition: all var(--transition-fast);
  border: none;
  flex: 1;
  font-weight: 600;
}

#notice div.notice-action button:hover {
  background: var(--color-primary-hover);
}

#notice div.notice-action button:active {
  transform: scale(.98);
}

#donate {
  display: none !important;
}

@media screen and (width <= 750px) {
  #settings {
    top: var(--space-3);
    left: var(--space-3);
    right: var(--space-3);
    min-width: 0;
    max-width: none;
    padding: var(--space-4);
    gap: var(--space-3);
  }

  #settings .settings-header {
    padding-bottom: var(--space-2);
  }

  #settings .settings-header .settings-title h1 {
    font-size: var(--font-size-lg);
  }

  #settings .settings-header .title-accent {
    height: 20px;
  }

  #settings .settings-grid {
    gap: var(--space-4);
    grid-template-columns: 1fr;
  }

  #settings .settings-left {
    gap: var(--space-2);
  }

  #settings textarea {
    min-height: 100px;
  }

  #settings .settings-right {
    padding-left: 0;
    padding-top: var(--space-3);
    border-left: none;
    border-top: 1px solid var(--color-border);
  }

  #settings .option-item.option-toggle {
    padding: var(--space-2) 0;
  }

  #settings .winner-controls {
    width: 100%;
  }

  #settings .winner-controls .btn-group {
    flex: 1;
  }

  #settings .settings-footer {
    padding-top: var(--space-3);
  }

  #notice {
    box-sizing: border-box;
    background: var(--color-notice-bg);
    z-index: 1001;
    width: 100%;
    max-width: 100%;
    height: 100%;
    padding: var(--space-4);
    color: var(--color-text-dark);
    border-radius: 0;
    flex-direction: column;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    transform: none;
  }

  #notice div.notice-body {
    flex-grow: 1;
    overflow-y: auto;
  }
}

div.toast {
  padding: var(--space-3) var(--space-4);
  border-radius: var(--radius-lg);
  background: var(--glass-bg);
  -webkit-backdrop-filter: blur(var(--glass-blur));
  border: 1px solid var(--glass-border);
  color: var(--color-text);
  font-family: var(--font-family);
  font-size: var(--font-size-sm);
  bottom: var(--space-4);
  z-index: 1000;
  box-shadow: var(--shadow-lg);
  animation: toast-in var(--transition-base) ease-out, toast-out var(--transition-base) ease-in 1s forwards;
  font-weight: 500;
  position: fixed;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%);
}

@keyframes toast-in {
  0% {
    opacity: 0;
    transform: translate(-50%, 100%);
  }

  100% {
    opacity: 1;
    transform: translate(-50%);
  }
}

@keyframes toast-out {
  0% {
    opacity: 1;
    transform: translate(-50%);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, 100%);
  }
}

